<template>
    <div class="ex-chang-box" v-loading="loading">
        <iframe :src="url" class="windows" :style="{height:height}" ref="ifr" @load="onLoad"></iframe>
    </div>
</template>

<script>
    export default {
        name: "exchangeCoupon",
        props: {
            height: {
                type: String,
                default: '',
            },
            loading: {
                type: Boolean,
                default: false
            },
            data: {
                type:Object,
                default: {}
            }
        },
        computed: {
            url: {
                get() {
                    let inte_id = this.data.hasOwnProperty('inte_id') ? this.data.inte_id : 0;
                    let prodId = this.data.hasOwnProperty('prodId') ? this.data.prodId : 0;
                    return this.$C('serverUrlAjax') + "/coupons/coupons_get.php?inte_id="+ inte_id +"&id="+ prodId +"&idweb=" + this.$getWebId() + "&act=show&lang=" + this.$lang() + "&v=9";
                }
            }
        },
        methods: {
            onLoad() {
                this.$refs.ifr.contentWindow.postMessage({msg: "isVue"}, this.$C('serverUrlAjax'));
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../assets/css/common/lib.scss';

    .ex-chang-box {
        position: relative;
        width: 100%;
        padding: 0;

        .windows {
            position: relative;
            width: 100%;
        }
    }
</style>