<template>
    <div class="ex-chang-box" v-loading="loading">
        <iframe :src="url" class="windows" :style="{height:height}" ref="ifr" @load="onLoad"></iframe>
    </div>
</template>

<script>
    export default {
        name: "exchangeBox",
        props: {
            height: {
                type: String,
                default: '',
            },
            loading: {
                type: Boolean,
                default: false
            },
            data: {
                type:Object,
                default: {}
            }
        },
        created(){
            if(this.$isMobile()){
                this.$router.push({name:"integralShop"});
            }
        },
        computed: {
            url: {
                get() {
                    let inte_id = this.data.hasOwnProperty('id') ? this.data.id : 0;
                    let type = this.data.hasOwnProperty('type') ? this.data.type : '';
                    return this.$C('serverUrlAjax') + "/exusers/points_store_checkout.php?inte_id="+ inte_id +"&idweb=" + this.$getWebId() + "&act=show&lang=" + this.$langID() +'&type='+ type + "&v=9";
                }
            }
        },
        methods: {
            onLoad() {
                this.$refs.ifr.contentWindow.postMessage({msg: "isVue"}, this.$C('serverUrlAjax'));
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../assets/css/common/lib.scss';

    .ex-chang-box {
        position: relative;
        width: 100%;
        padding: 0;

        .windows {
            position: relative;
            width: 100%;
        }
    }
</style>