<template>
    <div class="integral-style-one">
        <a class="img" href="#" @click="goHome" :style="{ background:'url('+data.img+') no-repeat center' } " v-if="data.img"></a>
        <a class="img no-img" href="#" @click="goHome" v-else></a>
        <div class="container">
            <p class="title">{{ data.name }}</p>
            <p class="money">{{ data.money }}+{{ data.integral }}{{ $L('积分') }}</p>
        </div>
        <div class="bottom">
            <el-button type="primary" size="small" v-if="data.Redemption === true && (data.inte_type == 1 || data.inte_type == 2)" @click="exchange(data.inte_id)">{{ $L('立即兑换') }}</el-button>
            <el-button type="primary" size="small" v-else-if="data.Redemption === true && (data.inte_type == 3)" @click="exchangeCoupon(data.inte_id,data.product_id)">{{ $L('立即兑换') }}</el-button>
            <el-button type="info" size="small" disabled v-else>{{ $L('立即兑换') }}</el-button>
        </div>
    </div>
</template>

<script>
    import exchangeBox from '../../point/exchangeBox.vue';
    import exchangeCoupon from '../../point/exchangeCoupon.vue';
    export default {
        name: "integralStyleOne",
        components:{
            exchangeBox,
            exchangeCoupon
        },
        props: {
            data: {
                type: Object,
                default: () => {
                    return {};
                }
            }
        },
        methods: {
            exchange(id){
                this.$store.commit('setDialogObj', {
                    name: this.$L('立即兑换'),
                    view: exchangeBox,
                    width: '96%',
                    height: '600px',
                    data: {id:id}
                });
            },
            exchangeCoupon(inte_id, prodId){
                this.$store.commit('setDialogObj', {
                    name: this.$L('兑换优惠卷'),
                    view: exchangeCoupon,
                    width: '96%',
                    height: '600px',
                    data: {inte_id:inte_id, prodId:prodId}
                });
            },
            goHome(){
                this.$gotoWebHome();
            },
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";
    .integral-style-one {
        font-size: 14px;
        display: flex;
        .img {
            display: block;
            color: #666;
            width: 100px;
            height: 100px;
            background-size: 100% 100% !important;
        }
        .container {
            width:calc(100% - 210px);
            padding: 0 10px 0 10px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .title {
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                line-clamp: 2;
                -webkit-box-orient: vertical;
                // padding-top: 5px;
            }
            .money {
                color: rgb(228, 11, 11);
                padding-bottom: 10px;
            }
        }

        .bottom {
            text-align: center;
            margin: auto;
            button{
                vertical-align: middle;
                width: 100%;
            }
        }
    }
</style>